<template>
  <div class="app_wapper">
      <el-container>
<!--        左侧菜单-->
        <el-aside class="slider_container">
          <el-menu class="sub_meuns_wapper" :default-active="$route.path" background-color="#304156" text-color="#fff" active-text-color="#409EFF">
<!--            菜单组件-->
              <side-munes></side-munes>
          </el-menu>
        </el-aside>
<!--        右侧页面-->
        <el-container class="app_content" style="margin-top: 0">
          <el-header style="text-align: right;font-size: 12px;">
            <i class="el-icon-setting" style="margin-right: 15px"></i>
            <el-button>退出</el-button>
          </el-header>
          <el-main>
            <router-view />
          </el-main>
        </el-container>
      </el-container>
  </div>
</template>

<script>
    import SideMunes from "@/components/sideMeuns";
    export default {
        name: "layout",
      components: {SideMunes},
      data(){
          return{

          }
      },
      mounted() {
        console.log(this.$route.path)
      }
    }
</script>

<style scoped>
  .app_wapper {
    position: relative;
    height: 100%;
    width: 100%;
  }

  .app_wapper .el-aside,
  .slider_container .el-menu {
    color: #333;
    transition: width .28s;
    width: 180px !important;
    height: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 1001;
    background-color: rgb(48, 65, 86);
  }
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  .app_content {
    min-height: 500px;
    margin-left: 170px;
    border: 1px solid #eee;
    box-sizing: border-box;
  }
</style>



